body{
    background-color: $colorSecondary;
    font-family:  'Montserrat', sans-serif;
}
.page-register{
    max-width: 1920px;
    width: 100%;
}

button {
    padding: 0;
    border: 0;
    background-color: transparent;
    cursor: pointer;
    margin-bottom: 16px;
    
    &:focus {
        outline: 0;
    }
}

img {
    width: 100%;
    display: block;
}

